"use client";

import { useState } from "react";
import Image from "next/image";
import axios from "axios";
import Link from "next/link";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  async function handleLogin(e: React.FormEvent) {
    e.preventDefault();
    setError("");
    setLoading(true);

    try {
      const res = await axios.post("/api/auth/login", {
        email,
        password,
      });

      // Save tokens
      localStorage.setItem("accessToken", res.data.accessToken);
      localStorage.setItem("refreshToken", res.data.refreshToken);
      localStorage.setItem("user", JSON.stringify(res.data.user));

      window.location.href = "/home";
    } catch (err: any) {
      setError(err.response?.data?.message || "Something went wrong");
    }

    setLoading(false);
  }

  return (
    <div className="min-h-screen grid grid-cols-1 md:grid-cols-2 font-[Inter]">
      {/* LEFT: Background Image */}
      <div className="relative h-64 md:h-auto">
        <Image
          src="/background-login.jpg"
          alt="Login Background"
          fill
          className="object-cover brightness-[0.85]"
          priority
        />
        <div className="absolute inset-0 bg-black/40"></div>

        <div className="absolute bottom-8 left-8 text-white drop-shadow-xl">
          <h1 className="text-5xl font-extrabold">Pehnava</h1>
          <p className="text-sm text-gray-200 max-w-xs">
            Step into your fashion world.
          </p>
        </div>
      </div>

      {/* RIGHT: Login Form */}
      <div className="flex items-center justify-center px-10 py-16 bg-gray-50">
        <div className="w-full max-w-md">
          <h2 className="text-3xl font-bold text-gray-900 mb-6">
            Login to your Account
          </h2>

          {error && (
            <div className="mb-4 text-red-600 text-sm font-medium">{error}</div>
          )}

          <form onSubmit={handleLogin} className="space-y-4">
            <div>
              <label className="text-sm font-semibold">Email</label>
              <input
                type="email"
                className="w-full mt-1 px-4 py-3 border rounded-xl bg-white"
                placeholder="you@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>

            <div>
              <label className="text-sm font-semibold">Password</label>
              <input
                type="password"
                className="w-full mt-1 px-4 py-3 border rounded-xl bg-white"
                placeholder="Enter your password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>

            <button
              type="submit"
              disabled={loading}
              className="w-full mt-4 px-6 py-3 bg-blue-600 text-white text-lg font-semibold rounded-xl shadow-lg hover:bg-blue-700 transition-all"
            >
              {loading ? "Logging in..." : "Login"}
            </button>
          </form>

          <p className="mt-5 text-sm text-gray-600">
            Donâ€™t have an account?{" "}
            <Link href="/register" className="text-blue-600 underline">
              Create one
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}
